:root {
    --primary: #fff;
   
}


.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
   
}

.btn--primary {
    
    color: #242424;
    border: 1px solid var(--primary);

}

.btn--outline {
  
    color: #fff;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
    background-color: transparent;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
    background-color: black;

}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
    background-color: transparent;
}

i {
    background-color: transparent;
}

.btn--medium:hover, .btn--large:hover {
    background: #fff;
    color: #242424;
    transition: all 0.3s ease-out;
}