.NavbarItem {
    background: transparent;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.4rem;
    position: sticky;
    top: 0;
    background-color: black;
}

.NavbarItem > h1{
    color: cornsilk;
    font-family: 'Montserrat', sans-serif;
    background-color: transparent;
}



.fa-hamburger {
    color: #fff;
    background-color: black;
    margin-top: 0px;
}

.fa-times {
    color: white;
    background-color: black;
}

.nav-menu{
    display: grid;
    background: transparent;
    grid-template-columns: repeat(4, auto);
    grid-gap: 15px;
    list-style: none;
    text-align: center;
    width: 65vw;
    justify-content: end;
    margin-right: 2rem;
    font-size: 1.2rem;
    background-color: black;

}


.nav-links{
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
    background-color: black;
}

.nav-links:hover {
    background-color: cornsilk;
    color: olive;
    border-radius: 2px;
    transition: all 0.1s ease-out;
    text-decoration: none;
}

.nav-links-mobile{
    display: none;
    background-color: black;
}

.menuIcon {
    display: none;
    background-color: black;
}


@media screen and (max-width: 900px) {
    .NavbarItem {
        position: relative;
        background-color: black;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 300px;
        position: absolute;
        top: 60px;
        left: -100%;
        transition: all 0.5s ease;
        background-color: black;
    }

    .nav-menu.active {
        background: #22232c;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
        background-color: black;
    }

    .nav-links {
        text-align: center;
        font-weight: bold;
        font-style: oblique;
        padding: 2rem;
        width: 100%;
        display: table;
        background-color: black;
    }

    .nav-links:hover{

        border-radius: 0;
        background-color: cornsilk;
    }

    .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(25%,50%);
        background-color: black;
    }


    .menuIcon{
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
        background-color: black;
    }

    .fa-times {
        color: white;
        font-size: 2rem;
        align-items: center;
        background-color: black;
    }

  
    Button {
        display: none;
        background-color: black;
    }

}
