
.body-container {
    background: url('./home.jpg') center center/cover no-repeat;  
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
}

.body-container > h1 {
    color: white;
    font-size: 30px;
    margin-top: 300px;
    background-color: transparent;
}

.body-buttons > btn {
    scroll-behavior: smooth;
}
